/* http://meyerweb.com/eric/tools/css/reset/
   v5.0.1 | 20191019
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, menu, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}

body {
  line-height: 1;
}

menu, ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}

* {
  box-sizing: border-box;
}

.srOnly {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
}

:root {
  --bg: #fff;
  --bg-top: #f7f9ff;
  --text-primary: #1d1f29;
  --text-secondary: #63687d;
  --switch-off: #aeb3cb;
  --switch-on: linear-gradient(225deg, #40db82 0%, #388fe7 98.02%);
  --switch-thumb: #f1f3fa;
  --switch-focus: #252b42;
  --separator: #848bab;
  --cell: #f1f3fa;
  --cell-hover: #e1e4f0;
  --positive: #1eb589;
  --negative: #dc414c;
}

body[theme=dark] {
  --bg: #1d1f29;
  --bg-top: #20222f;
  --text-primary: #fff;
  --text-secondary: #8c98c6;
  --switch-off: var(--switch-on);
  --switch-thumb: #252b42;
  --switch-focus: #f1f3fa;
  --separator: #333a55;
  --cell: #252b42;
  --cell-hover: #333a55;
}

body {
  font-family: Inter, sans-serif;
  line-height: normal;
}

.h1 {
  color: var(--text-primary);
  font-weight: 700;
  font-size: calc( 2.4rem + 0.4 * ((100vw - 37.5rem) / 106.5) );
  text-transform: capitalize;
}

.h2 {
  color: var(--text-secondary);
  font-weight: 700;
  font-size: 2.4rem;
  text-transform: capitalize;
}

body[theme=dark] .h2 {
  color: var(--text-primary);
}

.text {
  color: var(--text-secondary);
  font-weight: 700;
  font-size: 1.4rem;
  text-transform: capitalize;
}
.text-big {
  color: var(--text-primary);
  font-weight: 700;
  font-size: 5.6rem;
  line-height: 4.8rem;
  letter-spacing: -0.036em;
}
.text-secondary {
  color: var(--text-secondary);
  font-weight: 400;
  font-size: 1.2rem;
  letter-spacing: 0.42em;
  text-transform: uppercase;
}

.separator {
  width: 100%;
  height: 0.1rem;
  margin: 2.4rem 0 1.6rem;
  background: var(--separator);
  border: none;
}

.label {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.label > * + * {
  margin-left: 1.3rem;
}

.switch {
  position: relative;
  display: inline-block;
  width: 4.8rem;
  height: 2.4rem;
  background: var(--switch-off);
  border-radius: 1.2rem;
  cursor: pointer;
  transition: 0.15s;
}
.switch::after {
  position: absolute;
  top: 0.3rem;
  left: 2.6rem;
  width: 1.8rem;
  height: 1.8rem;
  background: var(--switch-thumb);
  border-radius: 50%;
  transition: left 0.25s;
  content: "";
}
.switch:hover {
  background: var(--switch-on);
}

input[type=checkbox]:focus + .switch {
  box-shadow: 0 0 0 0.2rem var(--switch-focus);
}
input[type=checkbox]:checked + .switch {
  background: var(--switch-on);
}
input[type=checkbox]:checked + .switch::after {
  left: 0.4rem;
}

.cell {
  display: flex;
  flex-flow: column;
  align-items: center;
  padding: 3.2rem 3.2rem 2.4rem;
  background: var(--cell);
  border-radius: 0.5rem;
  cursor: pointer;
  transition: background-color 0.2s;
}
.cell:hover {
  background-color: var(--cell-hover);
}

.account {
  display: flex;
  align-items: center;
  color: var(--text-secondary);
  font-weight: 700;
  font-size: 1.2rem;
}
.account::before {
  width: 2rem;
  height: 2rem;
  margin-right: 0.8rem;
  content: "";
}

.change {
  display: flex;
  align-items: center;
  font-weight: 700;
  font-size: 1.2rem;
}
.change::before {
  display: block;
  width: 0.8rem;
  height: 0.4rem;
  margin-right: 0.4rem;
  content: "";
}
.change[data-change=up] {
  color: var(--positive);
}
.change[data-change=up]::before {
  background: center/contain no-repeat url(icon-up.svg);
}
.change[data-change=down] {
  color: var(--negative);
}
.change[data-change=down]::before {
  background: center/contain no-repeat url(icon-down.svg);
}

.stat {
  color: var(--text-primary);
  font-weight: 700;
  font-size: 3.2rem;
}

.icon {
  width: 2rem;
  height: 2rem;
}

.wrapper {
  position: relative;
  min-height: 100vh;
  padding: 3.6rem 2.4rem 4.5rem;
  background: var(--bg);
}
.wrapper::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 23.5rem;
  background: var(--bg-top);
  border-radius: 0 0 2rem 2rem;
  content: "";
}
@media (min-width: 37.5rem) {
  .wrapper::before {
    height: 24.4rem;
  }
}

.container {
  position: relative;
  max-width: 111rem;
  margin: 0 auto;
}

.header {
  display: flex;
  flex-flow: column;
  justify-content: space-between;
}
@media (min-width: 37.5rem) {
  .header {
    flex-flow: row;
  }
  .header > .separator {
    display: none;
  }
}
.header > .label {
  transform: translateY(-2px);
}

.heading > * + * {
  margin-top: 0.4rem;
}
.heading > .h1 {
  transform: translateX(-2px);
}

.summary {
  margin-top: 4rem;
}
@media (min-width: 37.5rem) {
  .summary {
    margin-top: 4.6rem;
  }
}

.grid {
  display: grid;
  grid-gap: 2.4rem;
  grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
}
@media (min-width: 37.5rem) {
  .grid {
    grid-gap: 3rem;
  }
}

.card {
  position: relative;
  overflow: hidden;
}
.card::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 0.4rem;
  background: gray;
  content: "";
}
.card > .text-big {
  margin-top: 2.8rem;
}
.card > .text-secondary {
  margin-top: 0.9rem;
}
.card > .change {
  margin-top: 2.5rem;
}

.card[data-platform=facebook]::before {
  background: #178ff5;
}
.card[data-platform=facebook] > .account::before {
  background: center/contain no-repeat url(icon-facebook.svg);
}

.icon[data-platform=facebook] {
  background: center/contain no-repeat url(icon-facebook.svg);
}

.card[data-platform=twitter]::before {
  background: #1da1f2;
}
.card[data-platform=twitter] > .account::before {
  background: center/contain no-repeat url(icon-twitter.svg);
}

.icon[data-platform=twitter] {
  background: center/contain no-repeat url(icon-twitter.svg);
}

.card[data-platform=instagram]::before {
  background: linear-gradient(225deg, #df4896 0%, #ee877e 50.91%, #fdc366 100%);
}
.card[data-platform=instagram] > .account::before {
  background: center/contain no-repeat url(icon-instagram.svg);
}

.icon[data-platform=instagram] {
  background: center/contain no-repeat url(icon-instagram.svg);
}

.card[data-platform=youtube]::before {
  background: #c4032b;
}
.card[data-platform=youtube] > .account::before {
  background: center/contain no-repeat url(icon-youtube.svg);
}

.icon[data-platform=youtube] {
  background: center/contain no-repeat url(icon-youtube.svg);
}

.overview {
  margin-top: 4.6rem;
}
.overview > .grid {
  grid-gap: 1.6rem 2.4rem;
  margin-top: 2.4rem;
}
@media (min-width: 37.5rem) {
  .overview > .grid {
    grid-gap: 2.4rem 3rem;
  }
}
.overview .cell {
  padding: 2.6rem 3.1rem 1.9rem 2.4rem;
}
.overview .cell > * + * {
  margin-top: 2.1rem;
}
.overview .text {
  transform: translateY(-2px);
}
.overview .change {
  transform: translateY(7px);
}

.group {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}